import React, {useState} from "react";

export const AddCommentForm= ({articleName, setArticleInfo})=>{
    const [username, setUsername] = useState('')
    const [comment, setComment] = useState('')

    const addComment = async ()=>{
    const result = await fetch(`/api/articles/${articleName}/add-comment`,{
        method: "post",
        body: JSON.stringify({
            user: username,
            text: comment
        }),
        headers: {
            'Content-Type': 'application/json',
        }
    })
    const body = await result.json();
    setArticleInfo(body);
    setUsername("");
    setComment("")
}

    return (<div id="add-comment-form">
        <h3>Add a Comment</h3>
        <label htmlFor="">Name: <input type="text" name="" id="" value={username} onChange={(event) => setUsername(event.target.value)} /></label>
        <label htmlFor="">Comment: <textarea name="" id="" cols="30" rows="4" value={comment} onChange={(event) => setComment(event.target.value)} /></label>
        <button onClick={() => addComment()}>Add Comment</button>
    </div>)
}