import React from "react";

export const CommentsList= ({ comments })=>{
    return (
    <div>
    {comments &&
    <h3>Comments: </h3>}
    {comments &&
        comments.map((comment, k) => (
            <div className="comment" key={k}>
                <h4>{comment.username}</h4>
                <p>{comment.comment}</p>
            </div>
        ))
    }
    </div>);
}